import { useAppSelector } from '../app/hooks';
import { ReactComponent as Logo } from '../assets/logo.svg';
import { ReactComponent as Github } from '../assets/github.svg';
import { Local } from '../features/local/Local';
import { selectedLocal } from '../features/local/localSlice';
import { QURANAPI_URL, REPO_URL } from '../helpers/constants';

/**
 * Renders the top navigation bar component.
 * @returns {JSX.Element} The top navigation bar component.
*/
export default function Topbar(): JSX.Element {
    const local = useAppSelector(selectedLocal);

    return (
        <div dir='ltr' className="bg-body-tertiary col-12 d-flex flex-column justify-content-center mb-50">
            <div className=" d-flex justify-content-between p-2" style={{ height: 47 }} role="toolbar" aria-label="Toolbar with button groups">
                <div className="btn-group btn-group-sm" role="group" aria-label="Small button group">
                    <button type="button" className="btn btn-success disabled">2.0</button>
                    <a type="button" className="btn btn-outline-success" href={QURANAPI_URL}><span className='small bold fw-bold' >QuranAPI</span></a>
                    <a type="button" className="btn btn-outline-success" href={REPO_URL}><Github /></a>
                </div>
                <Local />
            </div>
                <Logo fill="#2C6030" className="top-icon" />
            <ul className="nav nav-pills justify-content-center">
                <li className="nav-item" >
                    <a className="nav-link nav-link-active" href={"#supports"}>{local.localizedStringsDict.supportsSectionTitle}</a>
                </li>
                <li className="nav-item" >
                    <a className="nav-link nav-link-active" href={"#versions"}>{local.localizedStringsDict.versionsSectionTitle}</a>
                </li>
                <li className="nav-item" >
                    <a className="nav-link nav-link-active" href={"#about"}>{local.localizedStringsDict.contactsSectionTitle}</a>
                </li>
            </ul>
        </div>
    );
}


