import React, { useEffect, useState } from 'react';
import { LOCALS_URL } from '../../helpers/constants';

import { useAppSelector, useAppDispatch } from '../../app/hooks';
import { selectedLocal, setLocal, LocalState, getStringsDictAsync } from './localSlice';
import { ReactComponent as Translate } from '../../assets/translate.svg';
import { capitalize } from '../../helpers/functions';

export function Local() {
    const local = useAppSelector(selectedLocal);

    const [locals, setLocals] = useState<LocalState[]>([]);

    const dispatch = useAppDispatch();

    const handleChange = (event: React.ChangeEvent<HTMLSelectElement>) => {
        const selection = locals.find(local => local.key === event.target.value);
        if (selection) {
            dispatch(getStringsDictAsync(selection))
            console.log(selection);
        } else {
            console.log("Option not found");
        }
    };

    useEffect(() => {
        fetch(LOCALS_URL)
            .then(response => response.json())
            .then(data => {
                setLocals(data)
                const selection = data.find((local: LocalState) => local.key === navigator.language.split('-')[0]);
                if (selection) {
                    dispatch(getStringsDictAsync(selection))
                    console.log(selection);
                } else {
                    const en_local = data.find((local: LocalState) => local.key === "en")!;
                    dispatch(getStringsDictAsync(en_local))
                    console.log(selection);
                }
            });
    }, []);

    return (
        <div className="input-group mb-3" style={{ width: 170 }}>
            <span className="input-group-text" id="basic-addon1"><Translate fill='#2C6030' /></span>
            <select className="form-select form-select-sm bg-success bg-opacity-50 " aria-label=".form-select-sm example" value={local.key} onChange={handleChange}>
                {locals.map(l => {
                    return (
                        <option value={l.key}>
                            <h3>
                                {capitalize(l.name)} {l.flag}
                            </h3>
                        </option>
                    );
                })}
            </select>        
        </div>
    );
}
