import { useEffect, useState } from "react";
import { CONTRIBUTORES_URL } from "../../helpers/constants";
import ProfileImage from "../ProfileImage";

export default function ContributionSection() {

    const [contributors, setContributors] = useState<{key: string, name: string, url?: string, src?: string}[]>([]);

    useEffect(() => {
        fetch(CONTRIBUTORES_URL)
          .then(response => response.json())
          .then(data => { console.log(data); setContributors(data)});
      }, []);

    return(
        <div className="d-flex flex-wrap">
            {
                contributors.map((contributor) => 
                     <ProfileImage src={contributor.src} url={contributor.url} legende={ contributor.name } />
                )
            }
        </div>
    )
}