
export const QURANAPI_URL = "https://quranapi-1-e0491334.deta.app" // will be https://quranapi.afrcvn.comwhen realised
// export const QURANAPI_URL = "http://127.0.0.1:8000" // for dev

const HOST = QURANAPI_URL + "/quran_app_api" 


export const REPO_URL = "http://github.com/xuraan/"
export const LOCALIZED_STRINGS_DICT_URL = (localCode: string) => HOST + "/locals/" + localCode + "/localized_strings_dict";


export const LOCALS_URL = HOST + "/locals"
export const CONTRIBUTORES_URL = HOST + "/contributors"
export const VERSION_URL =  (local: string) => HOST + "/versions/?local=" + local
export const FEATURES_URL = (local: string) => HOST + "/features/?local=" + local
export const QUESTIONS_URL = (local: string) => HOST + "/questions/?local=" + local
export const ANSWER_URL = (key: string, local: string) => HOST + "/answers/"+ key +"?local=" + local



export const fetchLocals = async () => {
    const response = await fetch(LOCALS_URL);
    return await response.json();
}