import { useEffect, useState } from "react";
import { ReactMarkdown } from "react-markdown/lib/react-markdown";
import remarkGfm from 'remark-gfm';
import { useAppSelector } from "../../app/hooks";
import { selectedLocal } from "../../features/local/localSlice";
import { ANSWER_URL, FEATURES_URL, QUESTIONS_URL } from "../../helpers/constants";
import ImageCard from "../ImageCard";

/**
 * Renders a support section for a website, with a list of features.
 * @returns {JSX.Element} A React component that displays a support section for a website.
 */
export default function SupportSection(): JSX.Element {
    const local = useAppSelector(selectedLocal);

    const [questions, setQuestions] = useState<{ key: string, string: string }[]>([]);
    const [selectedQuection, setSelectedQuection] = useState<{ key: string, string: string }>();
    const [data_bs_target, setDatBsTarget] = useState("");

    useEffect(() => {
        fetch(QUESTIONS_URL(local.key))
            .then(response => response.json())
            .then(data => setQuestions(data));
    }, [local]);

    return (
        <>
            <div className="d-flex flex-wrap">
                {questions.map((question, index) => {
                    return (
                        <button
                            style={{ textAlign: local.key === 'ar' ? "right" : 'left' }}
                            className={question.key == selectedQuection?.key ? "col-12 col-lg-6 btn  btn-success" : "col-12 col-lg-6 btn link"}
                            onClick={() => { setSelectedQuection(question); setDatBsTarget("#staticBackdrop") }}
                            data-bs-toggle="modal" data-bs-target={data_bs_target}
                        >
                            {question.string}
                        </button>
                    )
                })}
            </div>
            <AnswerModal key={selectedQuection?.key} string={selectedQuection?.string} />
        </>
    )
}


export function AnswerModal(props: { key?: string, string?: string }) {
    const local = useAppSelector(selectedLocal);
    const [answer, setAnswer] = useState<{ key: string, string: string, src: string }>();

    useEffect(() => {
        if (props.key) {
            fetch(ANSWER_URL(props.key, local.key))
                .then(response => response.json())
                .then(data => setAnswer(data));
        }
    }, [local]);

    return (
        <div className="modal fade" id="staticBackdrop" data-bs-backdrop="static" data-bs-keyboard="false" tabIndex={-1} aria-labelledby="staticBackdropLabel" aria-hidden="true">
            <div className="modal-dialog modal-dialog-centered modal-dialog-scrollable">
                <div className="modal-content">
                    <div className="modal-header">
                        <h1 className="modal-title fs-5" id="staticBackdropLabel">{props.string}</h1>
                        <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div className="modal-body">
                        <ReactMarkdown remarkPlugins={[remarkGfm]} >{answer?.string ?? ""}</ReactMarkdown>
                    </div>
                    <div className="modal-footer">
                        <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                        <button type="button" className="btn btn-primary">Understood</button>
                    </div>
                </div>
            </div>
        </div>
    )
}

/**
* Renders a feature section for a website, with an image and accompanying details.
* @param {Object} props - The props for the component.
* @param {string} props.id - The HTML ID of the section.
* @param {string} props.detail - The description of the feature.
* @param {string?} props.src - The source URL for the image.
* @param {string?} [props.className] - An optional CSS class name to apply to the component.
* @returns {JSX.Element} A React component that displays a feature section for a website.
*/
function Feature(props: { id: string, detail: string, src?: string, className?: string }): JSX.Element {
    return (
        <div id={props.id}>
            <div className={"d-flex flex-column flex-md-row justify-content-center align-items-center mb-5 mt-5" + props.className} >
                <div>
                    <ReactMarkdown remarkPlugins={[remarkGfm]} children={props.detail} />
                </div>
                <ImageCard src={process.env.PUBLIC_URL + props.src} className="feature-img" />
            </div>
        </div>
    );
}

