import ReactMarkdown from "react-markdown";
import remarkGfm from "remark-gfm";
import { useAppSelector } from "../../app/hooks";
import { selectedLocal } from "../../features/local/localSlice";

/**
 * The QuranApp About Section
 * @returns {JSX.Element} React component.
 */
export default function AboutSection(): JSX.Element {
    const local = useAppSelector(selectedLocal);

    return (
        <ReactMarkdown remarkPlugins={[remarkGfm]} >{local.localizedStringsDict.about ?? ""}</ReactMarkdown>
    )
}