import { ReactComponent as Person } from '../assets/person.svg';

export default function ProfileImage(props: {src?: string, legende?: string, url?: string}) {
    return(
        <a className='text-decoration-none text-center ' href={props.url ?? "#"} data-bs-toggle="tooltip" data-bs-placement="top" title={props.legende} >
            { (props.src?.length !== 0) ? <img className='profile' src={props.src} /> : <Person width={50} fill='gray' /> }
            {/* <div className='legende'>{props.legende}</div> */}
        </a>
    )
}