import { useEffect, useState } from 'react';
import { ReactMarkdown } from 'react-markdown/lib/react-markdown';
import remarkGfm from 'remark-gfm';
import { useAppSelector } from '../../app/hooks';
import { ReactComponent as FilledCircle } from '../../assets/circle.fill.svg';
import { selectedLocal } from '../../features/local/localSlice';
import { VERSION_URL } from '../../helpers/constants';

/**
*
* Renders a list of Quran versions fetched from an API QuranAPI and displays the details for each version.
* @returns {JSX.Element} A React component that displays the Quran versions.
*/
export default  function VersionSection(): JSX.Element {
    const local = useAppSelector(selectedLocal);

    const [versions, setVersions] = useState<{key: string, description: string}[]>([]);

    useEffect(() => {
        fetch(VERSION_URL(local.key))
          .then(response => { console.log(response); return response.json()})
          .then(data => setVersions(data));
      }, [local]);

    return(
        <>
            {
                versions.map((version, index) => { 
                    return (
                        <div key={index}>
                            <VersionDescription
                                number={version.key} 
                                description={version.description} 
                                isLastItem={ index === versions.length-1 ? true : false } 
                            />
                        </div>
                    ) 
                })
            }
        </>
    )
}

/**
 * Renders details for a Quran version, including its version number and description.
 * 
 * @param {Object} props - The props for the component.
 * @param {string} props.version - The version number.
 * @param {string} props.detail - The description of the version.
 * @param {boolean} props.isLastItem - A boolean value indicating whether this is the last item in the list.
 * @returns {JSX.Element} A React component that displays a feature infos.
 */
export function VersionDescription(props: {number: string, description: string, isLastItem: boolean}): JSX.Element{
    return(
        <div className="row">
            <div className='d-block d-flex flex-column align-items-center col-1' style={{ width: "25" }} >
                <div className='zstack'>
                    <FilledCircle fill="#2C6030" width="25" height="25" className="zic" />
                    <span className='ml-100 version-number' > {props.number}</span>
                </div>
                <div className="vr opac align-self-center h-100"></div>
                { props.isLastItem ? 
                    <div className='zstack'>
                        <FilledCircle fill="#2C6030" width="25" height="25" className="zic" />
                    </div> : null
                }
            </div>
        
            <div className="bg-body-tertiary col-11">
                <br />
                <ReactMarkdown remarkPlugins={[remarkGfm]} >{props.description}</ReactMarkdown>
         </div>
      </div>
    );
}
