import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit';
import { LOCALIZED_STRINGS_DICT_URL } from '../../helpers/constants';
import { RootState, AppThunk } from '../../app/store';

export const fetchLocalizedStringDict = async (localCode: string): Promise<LocalizedString> => {
    const response = await fetch(LOCALIZED_STRINGS_DICT_URL(localCode));
    return await response.json();
}

export interface LocalizedString {
    about?: string
    aboutSectionTitle?: string
    supportsSectionTitle?: string
    versionsSectionTitle?: string
    contactsSectionTitle?: string
    contributorsSectionTitle?: string
}

export interface LocalState {
    key: string;
    name: string;
    flag: string;
    localizedStringsDict: LocalizedString
}

const initialState: LocalState = {
    key: '',
    name: '',
    flag: '',
    localizedStringsDict: {}
};

export const getStringsDictAsync = createAsyncThunk(
    'local/getStringsDict',
    async (local: LocalState): Promise<LocalState> => {
      const response = await fetchLocalizedStringDict(local.key);
      return { ...local, localizedStringsDict: response };
    }
  );

export const localSlice = createSlice({
    name: 'local',
    initialState,
    // The `reducers` field lets us define reducers and generate associated actions
    reducers: {
        setLocal: (state, action: PayloadAction<LocalState>) => {
            const { flag, key, name, localizedStringsDict } = action.payload;
            state.flag = flag;
            state.key = key;
            state.name = name;
            state.localizedStringsDict = localizedStringsDict
        },
    },
    extraReducers: (builder) => {
        builder
        //   .addCase(incrementAsync.pending, (state) => {
        //     state.status = 'loading';
        //   })
          .addCase(getStringsDictAsync.fulfilled, (state, action) => {
            const { flag, key, name, localizedStringsDict } = action.payload;
            state.flag = flag;
            state.key = key;
            state.name = name;
            state.localizedStringsDict = localizedStringsDict
            console.log("here")
          })
        //   .addCase(incrementAsync.rejected, (state) => {
        //     state.status = 'failed';
        //   });
      },
});

export const { setLocal } = localSlice.actions;

export const selectedLocal = (state: RootState) => state.local;

export default localSlice.reducer;
